import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Skeleton from '@material-ui/lab/Skeleton';
import {
  Typography,
  Button,
  Card,
  CardMedia,
  IconButton,
} from '@material-ui/core';
import Message from '@material-ui/icons/PermPhoneMsgRounded';
import { green } from '@material-ui/core/colors';
import Photo from '../../assets/photo.jpg';
import Back from '@material-ui/icons/ArrowBackRounded';
import { Link } from 'react-router-dom';

const useStyles = makeStyles({
  root: {
    backgroundColor: 'white',
    borderRadius: '5px',
    padding: '40px',
  },
  cardWrapper: {
    width: '100%',
    justifyContent: 'center',
    display: 'flex',
  },
  photoWrapper: {
    width: '80%',
    height: '50vh',
  },
  photo: {
    width: '100%',
    height: '100%',
    objectFit: 'cover',
  },
  nameWrapper: {
    color: '#23467A',
    margin: '40px 0 20px 0',
  },
  detailsWrapper: {
    backgroundColor: '#F5F3F3',
    borderRadius: '5px',
    padding: '20px',
  },
  itemTitle: {
    color: '#23467A',
    marginRight: '5px',
  },
  itemValue: {
    color: '#5A5A5A',
  },
  itemWrapper: {
    display: 'flex',
    margin: '5px 0 5px 0',
  },
  textSkeleton: {
    margin: '5px 0 5px 0',
  },
  buttonWrapper: {
    width: '100%',
    marginTop: '20px',
  },
  button: {
    top: '20%',
    left: '70%',
    width: '30%',
    minHeight: '45px',
    color: 'white',
    fontWeight: 'bold',
    backgroundColor: green[500],
    '&:hover': {
      backgroundColor: green[600],
    },
  },
  iconButton: {
    backgroundColor: '#386FC2',
    '&:hover': {
      backgroundColor: '#386FC2',
    },
    color: 'white',
    height: '40px',
    width: '40px',
  },
});

const mockDetailsData = [
  { title: 'RGD', value: '1733' },
  { title: 'Num Filhas', value: '21' },
  { title: 'PTA Leite', value: '515' },
  { title: 'PTA Gordura', value: '11.5' },
  { title: 'PTA Proteina', value: '6.3' },
  { title: 'Centro de Inseminacao Atual', value: 'Alta Genetics' },
];

const Details = () => {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <div className={classes.backWrapper}>
        <Link to="/">
          <IconButton className={classes.iconButton}>
            <Back />
          </IconButton>
        </Link>
      </div>
      <div className={classes.cardWrapper}>
        <Card className={classes.photoWrapper}>
          <CardMedia className={classes.photo} image={Photo} />
        </Card>
      </div>
      <div className={classes.nameWrapper}>
        <Typography variant="h5">Jagunço IV FIV Shottle Alegre</Typography>
      </div>
      <div className={classes.detailsWrapper}>
        {mockDetailsData.map((item) => {
          return (
            <div className={classes.itemWrapper}>
              <Typography className={classes.itemTitle}>
                {`${item.title}:`}
              </Typography>
              <Typography className={classes.itemValue}>
                {item.value}
              </Typography>
            </div>
          );
        })}
        <Skeleton
          className={classes.textSkeleton}
          width={'80%'}
          variant="text"
        />
        <Skeleton
          className={classes.textSkeleton}
          width={'40%'}
          variant="text"
        />
        <Skeleton
          className={classes.textSkeleton}
          width={'60%'}
          variant="text"
        />
      </div>
      <div className={classes.buttonWrapper}>
        <Button startIcon={<Message />} className={classes.button}>
          Entre em Contato
        </Button>
      </div>
    </div>
  );
};

export default Details;
