import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Typography, TextField, Button } from '@material-ui/core';
import { green } from '@material-ui/core/colors';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { Link } from 'react-router-dom';

const useStyles = makeStyles({
  root: {
    backgroundColor: 'white',
    borderRadius: '5px',
  },
  headerWrapper: {
    padding: '50px',
  },
  label: {
    color: '#23467A',
  },
  labelWrapper: {
    margin: '20px 0 20px 0',
  },
  comboBox: {
    width: '60%',
  },
  optionWrapper: {
    padding: '10px 0 0 50px',
  },
  buttonWrapper: {
    width: '80%',
    padding: '70px',
  },
  button: {
    width: '35%',
    minHeight: '40px',
    color: 'white',
    fontWeight: 'bold',
    backgroundColor: green[500],
    '&:hover': {
      backgroundColor: green[600],
    },
  },
});

const mockOptions = [
  'Grau de Sangue',
  'PTA Leite',
  'PTA Gordura',
  'PTA Proteína',
  'ICC',
  'Num. Filhas',
];

const Search = () => {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <div className={classes.headerWrapper}>
        <Typography className={classes.label} variant="h5">
          Selecione as principais características desejadas:
        </Typography>
      </div>
      {[1, 2, 3].map((o, index) => {
        return (
          <div key={index} className={classes.optionWrapper}>
            <div className={classes.labelWrapper}>
              <Typography className={classes.label} variant="h7">
                Característica {o}:
              </Typography>
            </div>
            <Autocomplete
              className={classes.comboBox}
              options={mockOptions}
              getOptionLabel={(option) => option}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Selecione..."
                  variant="outlined"
                />
              )}
            />
          </div>
        );
      })}
      <div className={classes.buttonWrapper}>
        <Link to="/home">
          <Button className={classes.button}>Buscar Melhores Opções</Button>
        </Link>
      </div>
    </div>
  );
};

export default Search;
