import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Container,
  Card,
  CardContent,
  CardMedia,
  Typography,
  Fab,
  ButtonBase,
} from '@material-ui/core';
import { green } from '@material-ui/core/colors';
import Skeleton from '@material-ui/lab/Skeleton';
import SearchRoundedIcon from '@material-ui/icons/SearchRounded';
import Banner from '../../assets/home-banner.jpg';
import { Link } from 'react-router-dom';

const useStyles = makeStyles({
  root: {
    backgroundColor: 'white',
    borderRadius: '5px',
  },
  container: {
    width: '90%',
    padding: '30px 0 30px 0',
  },
  banner: {
    marginBottom: '30px',
  },
  bannerMedia: {
    height: '385px',
    width: 'auto',
  },
  labelPrimary: {
    color: '#23467A',
    marginBottom: '10px',
  },
  labelSecondary: {
    color: '#5A5A5A',
    marginBottom: '5px',
  },
  itemCard: {
    padding: '15px',
    margin: '10px',
    display: 'flex',
    width: '100%',
  },
  itemImg: {
    flex: 2,
  },
  itemContent: {
    flex: 3,
    display: 'grid',
    textAlign: 'initial',
  },
  fab: {
    position: 'fixed',
    bottom: '4%',
    right: '28%',
    color: 'white',
    fontWeight: 'bold',
    backgroundColor: green[500],
    '&:hover': {
      backgroundColor: green[600],
    },
  },
  buttonBase: {
    width: '100%',
  },
});

// Fake data for prototype purposes:
const mockData = [
  {
    name: 'Jagunço',
    breed: 'Girolando',
  },
  {
    name: 'Benedito',
    breed: 'Gir Leiteiro',
  },
  {
    name: 'Conde',
    breed: 'Girolando',
  },
];

const Home = () => {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Container className={classes.container}>
        <Card className={classes.banner}>
          <CardMedia
            className={classes.bannerMedia}
            image={Banner}
            title="Alta Genetics Banner"
          />
          <CardContent>Alta Genetics Banner</CardContent>
        </Card>
        <Typography variant="h5" className={classes.labelPrimary}>
          Últimos animais consultados e favoritos:
        </Typography>
        <div>
          {mockData.map((data) => {
            return (
              <Link to="/details">
                <ButtonBase className={classes.buttonBase}>
                  <Card className={classes.itemCard}>
                    <div className={classes.itemImg}>
                      <Skeleton variant="rect" width={'90%'} height={'100%'} />
                    </div>
                    <div className={classes.itemContent}>
                      <Typography variant="h6" className={classes.labelPrimary}>
                        {data.name}
                      </Typography>
                      <Typography
                        variant="h7"
                        className={classes.labelSecondary}
                      >{`Raça: ${data.breed}`}</Typography>
                      <Skeleton
                        className={classes.labelSecondary}
                        width={'60%'}
                        variant="text"
                      />
                      <Skeleton
                        className={classes.labelSecondary}
                        width={'80%'}
                        variant="text"
                      />
                    </div>
                  </Card>
                </ButtonBase>
              </Link>
            );
          })}
        </div>
        <Link to="/search">
          <Fab className={classes.fab} variant="extended">
            <SearchRoundedIcon />
            Buscar
          </Fab>
        </Link>
      </Container>
    </div>
  );
};

export default Home;
